import Axios from "axios";
import Swal from "sweetalert2";

class SumateService {
  get token() {
    return localStorage.getItem("token") || "";
  }

  get headers() {
    return {
      headers: {
        token: this.token,
      },
    };
  }

  async getSumate() {
    return await Axios.get(`${process.env.REACT_APP_URL}/sumate`, this.headers)
      .then(({ data }) =>
        data.sumate.filter((noticias) => noticias._id).reverse()
      )
      .catch((e) => {
        console.log(e);
        return [];
      });
  }

  async crearSumate(sumate, token) {
    return await Axios.post(`${process.env.REACT_APP_URL}/sumate`, {
      sumate,
      token,
    })
      .then(({ data }) => {
        console.log("la data de sumate y el token ", data);
        if (data.ok)
          return Swal.fire(
            "envio con exito",
            "te contactaremos en breve !!",
            "success"
          );
        else return Swal.fire("error al enviar el formulario ", "", "error");
      })
      .catch((e) => {
        console.log(e);
        return Swal.fire(
          "ocurrio un error",
          "estamos trabajando para repararlo :)",
          "error"
        );
      });
  }

  borrarSumate(_id) {
    const url = `${process.env.REACT_APP_URL}/sumate/${_id}`;
    return Axios.delete(url, this.headers)
      .then(() => alert("Borrado", "", "success"))
      .catch((e) => {
        console.log(e);
        return null;
      });
  }

  async actualizarSumate(id, newSumate) {
    return await Axios.put(
      `${process.env.REACT_APP_URL}/sumate/${id}`,
      newSumate,
      this.headers
    ).catch((e) => {
      console.log(e);
      return null;
    });
  }
}

export default new SumateService();
